.App {
  text-align: center;
  /* max-width: 100vh; */
  margin: auto;
}

.paginationBttns {
  width: 100%;
  display: flex;
  list-style: none;
  justify-content: center;
  margin: 2% 0;
  background-color: transparent;
}
.paginationBttns a {
  /* border: 2px solid whitesmoke; */
  /* padding: 5px 15px; */
  border-radius: 4px;
  border: 0;
  margin: 0.5rem;
  background-color: whitesmoke;
  color: grey;
  /* padding:0.6rem; */
  max-width: 2rem;
  max-height: 2rem;
}

.paginationActive a {
  /* DA ESTILO CUANDO BOTON(Nº) ESTÁ ACTIVO CON < ó >   */
  color: black;
  background-color: #81d4fa;
}

.paginationActive a:focus {
  /* DA ESTILO CUANDO BOTON(Nº) ESTÁ ACTIVO CON CLICK DE MOUSE   */
  background-color: #81d4fa;
  color: black;
}

.modal-body,
.modal-header,
.modal-footer {
  background-color: #181818;
  color: yellow;
}

button.btn-close {
  background-color: grey;
}

button.btn-close:hover {
  background-color: whitesmoke;
  opacity: 0.4;
}
.active {
  /* CSS CUANDO NAVLINK ESTA ACTIVE */
  border-radius: 2px;
}

.modal-footer {
  justify-content: space-around;
}
